/* display background color black on navbar scroll */
.navbarScroll.navbarDark {
    background-color: black;
}

/* hero background image */
.bgimage {
    height:100vh;
    background: url('images/heroImage.jpeg');
    background-size:cover;
    position:relative;
}
/* text css above hero image*/
.hero_title {
    font-size: 4rem;
}
.hero_desc {
    font-size: 3rem;
}
.hero-text {
    text-align: center;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    color: green;
}


/* about section image css */
.imageAboutPage {
    width: 100%;
    font-size: medium;
}

/* social media icons styling */
.social-icons {
    font-size: 50px;
    cursor: pointer;

}
.fa-twitter:hover,.fa-linkedin:hover, .fa-twitter:hover,.fa-github:hover {
    color: #0f9f0f;
}
.fab {
    color: #000000;
}
/* footer styling */
#footer {
    background-color: #808080;
    text-align: center;
}

